import styled from "styled-components";
import { Row, Col } from 'react-bootstrap';
import NFTCard from "../../../components/NFTCard";
import Dropdown from "../../../components/UI/Dropdown";
import {useRef, useEffect, useState, useCallback} from "react";
import { OrderSide } from 'opensea-js/lib/types';

const HeaderCol = styled(Col)`
  margin-bottom: 20px;
`

const NoResultText = styled.span`
  color: white;
  font-size: 1rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 14px;
`

const NoResultDescription = styled.span`
  color: rgba(255, 255, 255, 0.5);
  font-size: .875rem;
  font-weight: 400;
  text-align: center;
`

const sortItems = [
    {
        title: "Recently Created",
        value: 'recently_created',
    },
    {
        title: "Lowest Price",
        value: 'lowest_price',
    },
    {
        title: "Highest Price",
        value: 'highest_price',
    },
    {
        title: "Oldest",
        value: 'oldest',
    },
]



const Orders = props => {
    const loader = useRef(null);
    const { fetchOrders, page, increasePage, setSort, orders, hasMore } = props;

    const changeSort = (value) => {
        setSort(value);
    }

    const observeAction = () => {
        increasePage();
    }

    const handleObserver = (entities) => {
        const target = entities[0];
        if (target.isIntersecting) {
            observeAction();
        }
    }

    useEffect(() => {
        fetchOrders();
    }, [props.selectedCollection])

    useEffect(() => {
        fetchOrders();
    }, [page, props.seaport])


    useEffect(() => {
        const options = {
            root: null,
            rootMargin: '20px',
            threshold: 0,
        }

        const observer = new IntersectionObserver(handleObserver, options);
        if (loader.current) {
            observer.observe(loader.current)
        }
    }, [])


    return (
        <Row className={'custom-row'}>
            <HeaderCol xs={12} className={'d-flex align-items-end justify-content-end'}>
                <Dropdown placeholder={'Sort by'} variant={'secondary'} items={sortItems} onChange={changeSort}/>
            </HeaderCol>

            {orders.map((order, i) => {
                return (
                    <Col key={`nft-order-${i}`} xs={12} md={6} lg={3}>
                        <NFTCard
                            seaport={props.seaport}
                            order={order}
                        />

                    </Col>
                )
            })}
            <Col xs={12} ref={loader}>
                <Row className="custom-row">
                    {hasMore && [...Array(orders.length > 0 ? 4 : 8)].map((item, i) => {
                        return (
                            <Col key={`nft-loading-${i}`} xs={12} md={6} lg={3}>
                                <NFTCard
                                    loading={true}
                                />

                            </Col>
                        )
                    })}
                    {!hasMore && orders.length === 0 && (
                        <Col xs={12} className={'d-flex flex-column align-items-center justify-content-center py-5'}>
                            <NoResultText>There are no assets</NoResultText>
                            <NoResultDescription>
                                {props.selectedCollection ? 'Please change selected collection or try later.' : 'Please try later to see new offers'}
                            </NoResultDescription>
                        </Col>
                    )}
                </Row>
            </Col>
        </Row>
    )
}

export default Orders;
